<template>
  <div>
    <div v-for="class1 in objArr">
      <input type="checkbox" v-model="class1.checked">{{class1.name}}
    </div>
  </div>
</template>

<script>

export default {
  name: "loginPage",
  data() {
    return {
      value: "normal",
      objArr: [],
    };
  },
  created() {
    console.log('created');
    var objArr = [
      {
        name:'food',
        checked:false,
        children:[
          {
            name:'fruits',
            checked:false,
            children:[
              {
                name:'banana',
                checked:false,
                children:[]
              }
            ]
          },
        ]
      },
      {
        name:'animal',
        checked:false,
        children:[
          {
            name:'fruits',
            checked:false,
            children:[
              {
                name:'banana',
                checked:false,
                children:[]
              }
            ]
          },
        ]
      },


    ]

    this.objArr = objArr
    console.log(objArr);

  },
  mounted() {
  },
  methods: {

  }
};
</script>

<style lang="scss">
.loginPage button {
  border-radius: 0px 0px 4px 4px;
}

.loginPage .el-button span {
  font-size: $font-size-14px !important;
}

.loginPage .el-input__inner {
  border-style: none solid;
  border-radius: 0;
}
</style>

<style scoped lang="scss">
.loginPage {
  width: 100vw;
  height: 100vh;
  background: $cover-bakcground-color;
}

.loginPageContent {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80vw;
}
.loginPageImg {
  width: 100%;
  height: 20vh;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  -moz-background-size: 100% 100%;
}
button {
  width: 100%;
  background-color: #dedede;
  color: #1880fe;
}

.selectFontSize {
  float: right;
}

.selectFontSize span {
  color: $blue-color;
}
</style>



